@import "./_job.less";
@import "./module/fn-m-job-operation-bar";
@import "./_job.less";
@job-img-dir: 'job/_images';

.forget-word{
	width:96%;
	.margin(2%);
	color:#666666;
	line-height:25*@rex;
}

.forget-pass{
	background:#fff;
	border-top:1*@rex solid #D5D5D5;
	overflow:hidden;

	.forget{
		border-bottom:1*@rex solid #D5D5D5;
		line-height:45*@rex;
		overflow:hidden;
		position:relative;

		.f-name{
			width:80*@rex;
			float:left;
			display:block;
			color:#666666;
			text-indent:5*@rex;
		}

		input[type=text]{
			float:left;
			border:none;

			width:160*@rex;
			line-height:45*@rex;
		}

		.code-pc{
			display:block;
			.size(70*@rex,29*@rex);
			float:left;
			background:#CCD7DC;
			.margin(8*@rex,_,8*@rex,_);
			.absolute(_,12%,_,_);
		}

		.loading{
			display:block;
			background: data-uri('@{job-img-dir}/ico-3.png');
			.size(30*@rex,25*@rex);
			background-size:100% 100%;
			.absolute(_,2%,_,_);
			.margin(10*@rex,_,10*@rex,_);
			transition:All 0.3s ease-in-out;
			animation:mymove 3s infinite;
		}

		@keyframes mymove{
			100% {transform:rotate(360deg); }
		}

		.code-pc-1{
			display:block;
			.size(84*@rex,30*@rex);
			background:#999999;
			border-radius:4*@rex;
			line-height:30*@rex;
			color:#fff;
			text-align:center;
			.absolute(8*@rex,45*@rex,_,_);
			text-decoration:none;
			cursor:pointer;

			&.bg-red{
				background:@main-color;
			}
		}
	}



	.r-choose{
		overflow:hidden;
		position:relative;

		&.active{
			.r-hide{
				display:block;
			}

			.r-down{
				background-image: data-uri('@{job-img-dir}/r-down.png')!important;
			}

		}

		.r-bttom{

			border-bottom:1*@rex solid #D5D5D5;
			overflow:hidden;

			.r-code{
				display:block;
				line-height:45*@rex;
				float:left;
				color:#666666;
				text-indent:5*@rex;
			}

			.r-down{
				display:block;
				background: data-uri('@{job-img-dir}/r-up.png');
				background-size:100% 100%;
				.size(14*@rex,8*@rex);
				.absolute(18*@rex,2%,18*@rex,_);



			}
		}

		.r-hide{
			display:none;
			background:#eee;
			overflow:hidden;
			width:100%;

			li{
				display:block;
				line-height:45*@rex;
				border-bottom:1*@rex solid #D5D5D5;
				color:#0F0F0F;
				text-indent:45*@rex;
			}
		}
	}

}

a.btn{
	display:block;
	width:94%;
	.margin(5%,3%,_,3%);
	background:#C20053;
	border-radius:4*@rex;
	line-height:40*@rex;
	color:#fff;
	border:none;
	text-align:center;
}

a.down{
	display:block;
	width:94%;
	.margin(5%,3%,_,3%);
	background:#999999;
	border-radius:4*@rex;
	line-height:40*@rex;
	color:#fff;
	border:none;
	text-align:center;
}

